.container {
    display: flex;
    justify-content: space-around;
   
}
@media screen and (min-width: 768px) {
    .container {
        
        margin-left: 173px;
    }
}

@media screen and (min-width: 1440px) {
    .container {
        padding-left: 0;
    }
}

.list {
   margin-bottom: 32px;
   display: flex;
   flex-direction: column;
   align-items: center;
}

.item:not(:last-child){
    padding-bottom: 14px;
}

.item_link {
 color: #FAFAFA;
 text-decoration: none;
 font-size: 14px;
 font-weight: 500;
 line-height: 18px;
 letter-spacing: -0.02em;
 text-align: left;

}

@media screen and (min-width: 768px) {
    .list {
       align-items:baseline; 
       
    }
}
